/**
 * @author 吴永军
 * @description 订单管理路由
 *  */
export default {
  path: '/manageOrders',
  name: '订单管理',
  component: resolve => require(['../views/manageOrders/ManageOrders.vue'], resolve),
  redirect: '/serviceOrders',
  children:
  [
    {
      path: '/serviceOrders',
      name: '服务订单',
      component: resolve => require(['../views/manageOrders/orders/ServiceOrders.vue'], resolve)
    },
    {
      path: '/serviceOrderDetail/:orderId',
      name: '订单详情',
      component: resolve => require(['../views/manageOrders/orders/ServiceOrderDetail.vue'], resolve)
    },
    {
      path: '/courseOrders',
      name: '课程订单',
      component: resolve => require(['../views/manageOrders/orders/CourseOrders.vue'], resolve)
    },
    {
      path: '/previewElecContract',
      name: '查看电子合同',
      component: resolve => require(['../views/manageOrders/orders/PreviewContract.vue'], resolve)
    }
  ]
}



