/**
 * @author 吴永军
 * @description 课程管理路由
 *  */
export default {
  path: '/manageCourses',
  name: '课程管理',
  component: resolve => require(['../views/manageCourses/ManageCourses.vue'], resolve),
  redirect: '/courses',
  children:
  [
    {
      path: '/courses',
      name: '课程信息',
      component: resolve => require(['../views/manageCourses/courses/Courses.vue'], resolve)
    },
    {
      path: '/courseDetail/:courseId',
      name: '课程详情',
      component: resolve => require(['../views/manageCourses/courses/CourseDetail.vue'], resolve)
    }
  ]
}



