export default {
  path: '/manageDistribution',
  name: '分销管理',
  component: resolve => require(['../views/manageDistribution/ManageDistribution.vue'], resolve),
  redirect: '/distributionRecords',
  children:
  [
    {
      path: '/distributionRecords',
      name: '分销列表',
      component: resolve => require(['../views/manageDistribution/distributionRecord/DistributionRecords.vue'], resolve)
    },
    {
      path: '/distributionDetails/:id',
      name: '分销详情',
      component: resolve => require(['../views/manageDistribution/distributionRecord/DistributionDetails.vue'], resolve)
    }
  ]
}



