import Vue from 'vue'
import Router from 'vue-router'

// 业务路由
import manageAyi from './router-manageAyi'
import manageDemand from './router-manageDemand'
import manageOrders from './router-manageOrders'
import manageDistribution from './router-manageDistribution'
import myAccount from './router-myAccount'
import manageCredit from './router-managerCredit'
import settingSystem from './router-settingSystem'
import manageComplain from './router-manageComplain'
import manageCourses from './router-manageCourses'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/login',
      name: 'login',
      component: resolve => require(['../views/Login.vue'], resolve)
    },
    {
      path: '/regist',
      name: 'regist',
      component: resolve => require(['../views/Regist.vue'], resolve)
    },
    {
      path: '/settleIn',
      name: 'settleIn',
      component: resolve => require(['../views/SettleIn.vue'], resolve)
    },
    {
      path: '/home',
      name: 'home',
      component: resolve => require(['../views/Home.vue'], resolve)
    },
    {
      path: '/app',
      name: '程序',
      component: resolve => require(['../views/Application.vue'], resolve),
      redirect: '/Aunt',
      children: [manageAyi, manageDemand, manageDistribution, myAccount, manageCredit, settingSystem, manageOrders, manageComplain, manageCourses]
    },
    {
      path: '*',
      redirect: '/login'
    }
  ]
})



