export default {
  path: '/manageComplain',
  name: '投诉管理',
  component: resolve => require(['../views/manageComplain/ManageComplain.vue'], resolve),
  redirect: '/orderComplain',
  children:
  [
    {
      path: '/orderComplain',
      name: '订单申诉',
      component: resolve => require(['../views/manageComplain/orderComplain/OrderComplain.vue'], resolve)
    },
    {
      path: '/complainDetails',
      name: '申诉详情',
      component: resolve => require(['../views/manageComplain/orderComplain/ComplainDetails.vue'], resolve)
    }
    // {
    //   path: '/interveneDispose',
    //   name: '介入',
    //   component: resolve => require(['../views/manageComplain/orderComplain/InterveneDisPose.vue'], resolve)
    // },
    // {
    //   path: '/finishService',
    //   name: '终止服务',
    //   component: resolve => require(['../views/manageComplain/orderComplain/FinishService.vue'], resolve)
    // }
  ]
}



