export default {
  path: '/manageDemand',
  name: '需求列表',
  component: resolve => require(['../views/manageDemand/ManageDemand.vue'], resolve),
  redirect: '/demandRecords',
  children:
  [
    {
      path: '/demandRecords',
      name: '需求列表',
      component: resolve => require(['../views/manageDemand/demands/DemandRecords.vue'], resolve)
    },
    {
      path: '/releaseManagement',
      name: '需求添加',
      component: resolve => require(['../views/manageDemand/demands/ReleaseManagement.vue'], resolve)
    },
    {
      path: '/demandDetail/:id',
      name: '需求详情',
      component: resolve => require(['../views/manageDemand/demands/DemandDetail.vue'], resolve)
    },
    {
      path: '/demandEdit/:id',
      name: '需求修改',
      component: resolve => require(['../views/manageDemand/demands/DemandEdit.vue'], resolve)
    }
  ]
}



