export default {
  path: '/Aunt',
  name: 'Aunt',
  component: resolve => require(['../views/AuntManagement/Aunt'], resolve),
  redirect: '/AuntList',
  children: [{
    path: '/AuntList',
    name: 'AuntList',
    component: resolve => require(['../views/AuntManagement/AuntList'], resolve)
  }, {
    path: '/AuntDetail/:id',
    name: 'AuntDetail',
    component: resolve => require(['../views/AuntManagement/AuntDetail'], resolve)
  }, {
    path: '/createAunt/:id',
    name: 'CreateAunt',
    component: resolve => require(['../views/AuntManagement/EnteringAunt.vue'], resolve)
  }, {
    path: '/BlackList',
    name: 'BlackList',
    component: resolve => require(['../views/AuntManagement/BlackList'], resolve)
  }]
}



