export default {
  path: '/credit',
  name: '信用管理',
  component: resolve => require(['../views/managerCredit/ManageCredit.vue'], resolve),
  redirect: '/creditRecord',
  children:
  [
    {
      path: '/creditRecord',
      name: '信用记录',
      component: resolve => require(['../views/managerCredit/credit/CreditRecord.vue'], resolve)
    }
  ]
}



