import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    slideToggle: false,
    image: '', // 上传的图片地址
    ossUrl: '', // oss图片资源访问路径
    topTitles: [],
    orderStatus: {
      serviceStatus: {},
      payStatus: {}
    }
  },
  getters: {
    getImage (state) {
      return state.image
    },
    getOssUrl (state) {
      return state.ossUrl
    },
    getOrderStatus (state) {
      return state.orderStatus
    }
  },
  mutations: {
    changeSlide (state) {
      state.slideToggle = !state.slideToggle
    },
    changeImage (state, newImage) {
      state.image = newImage
    },
    changeOssUrl (state, url) {
      state.ossUrl = url
    },
    changeTopTitles (state, data) {
      state.topTitles = data
    },
    changeOrderStatus (state, status) {
      state.orderStatus = status
    }
  },
  actions: {
    commitChangeSlide (content) {
      content.commit('changeSlide')
    },
    commitChangeImage ({ commit }, newImage) {
      commit('changeImage', newImage)
    },
    commitChangeOssUrl ({ commit }, url) {
      commit('changeOssUrl', url)
    },
    commitChangeTopTitles ({ commit }, data) {
      commit('changeTopTitles', data)
    },
    commitChangeOrderStatus ({ commit }, status) {
      commit('changeOrderStatus', status)
    }
  }
})



