export default {
  path: '/system',
  name: '系统设置',
  component: resolve => require(['../views/settingSystem/SystemSetting.vue'], resolve),
  redirect: '/essentialInfo',
  children:
  [
    {
      path: '/essentialInfo',
      name: '基本信息',
      component: resolve => require(['../views/settingSystem/system/EssentialInformation.vue'], resolve)
    },
    {
      path: '/versionMan',
      name: '版本管理',
      component: resolve => require(['../views/settingSystem/system/VersionManager.vue'], resolve)
    },
    {
      path: '/changePwd',
      name: '修改密码',
      component: resolve => require(['../views/settingSystem/system/ChangePassword.vue'], resolve)
    },
    {
      path: '/accountMan',
      name: '账号管理',
      component: resolve => require(['../views/settingSystem/system/AccountManagement.vue'], resolve)
    },
    {
      path: '/addAccount',
      name: '新增管理员',
      component: resolve => require(['../views/settingSystem/system/AddAccount.vue'], resolve)
    }
  ]
}



