// 获取 用户信息
export const getLoginInfo = key => {
  if (!sessionStorage.getItem('userInfo')) {
    return ''
  }
  const userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
  var userData = ''
  Object.keys(userInfo).forEach(ele => {
    if (ele === key) {
      userData = userInfo[ele]
      return userData
    } else if (userInfo[ele] && typeof userInfo[ele] === 'object') {
      Object.keys(userInfo[ele]).forEach(ke => {
        if (ke === key) {
          userData = userInfo[ele][ke]
          return userData
        } else if (userInfo[ele][ke] && typeof userInfo[ele][ke] === 'object') {
          Object.keys(userInfo[ele][ke]).forEach(k => {
            if (k === key) {
              userData = userInfo[ele][ke][k]
              return userData
            }
          })
        }
      })
    }
  })
  return userData
}
export const saveLoginInfo = (key, value) => {
  const userInfo = JSON.parse(sessionStorage.getItem('userInfo'))
  if (!userInfo) {
    return ''
  }
  Object.keys(userInfo).forEach(ele => {
    if (ele === key) {
      userInfo[ele] = value
      sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
      return ''
    } else if (userInfo[ele] && typeof userInfo[ele] === 'object') {
      Object.keys(userInfo[ele]).forEach(ke => {
        if (ke === key) {
          userInfo[ele][ke] = value
          sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
          return ''
        } else if (userInfo[ele][ke] && typeof userInfo[ele][ke] === 'object') {
          Object.keys(userInfo[ele][ke]).forEach(k => {
            if (k === key) {
              userInfo[ele][ke][k] = value
              sessionStorage.setItem('userInfo', JSON.stringify(userInfo))
              return ''
            }
          })
        }
      })
    }
  })
}
