export const timeStrSafari = time => {
  // 全部“-”替换为 “/”
  return time.replace(/-/g, '/')
}

export const getFormate = () => {
  const weeks = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
  let date = new Date()
  let month = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : '0' + (date.getMonth() + 1)
  let days = date.getDate() > 9 ? date.getDate() : '0' + date.getDate()
  let day = weeks[date.getDay()]
  // let hours = date.getHours() > 9 ? date.getHours() : '0' + date.getHours()
  // let minute = date.getMinutes() > 9 ? date.getMinutes() : '0' + date.getMinutes()
  // let sends = date.getSeconds() > 9 ? date.getSeconds() : '0' + date.getSeconds()
  return month + '月' + days + '日 ' + day
}
export const formatDate = (dateStr, pattern = 'yyyy-MM-dd hh:mm:ss') => {
  let patterns = {
    'yyyy年MM月dd日': ['年', '月', '日'],
    'yyyy年MM月dd日 hh:mm:ss': ['年', '月', '日'],
    'yyyy-MM-dd': '-',
    'yyyy/MM/dd': '/',
    'yyyy.MM.dd': '.',
    'yyyy-MM-dd hh:mm:ss': '-',
    'yyyy/MM/dd hh:mm:ss': '/',
    'yyyy.MM.dd hh:mm:ss': '.'
  }
  let str = ''
  dateStr.indexOf('T') !== -1 ? str = dateStr : str = dateStr.replace(/-/g, '/')
  // console.log(str)
  let date = new Date(str)
  let year = date.getFullYear()
  let month = date.getMonth() + 1 > 9 ? date.getMonth() + 1 : `0${date.getMonth() + 1}`
  let day = date.getDate() > 9 ? date.getDate() : `0${date.getDate()}`
  let hour = date.getHours() > 9 ? date.getHours() : `0${date.getHours()}`
  let minute = date.getMinutes() > 9 ? date.getMinutes() : `0${date.getMinutes()}`
  let seconds = date.getSeconds() > 9 ? date.getSeconds() : `0${date.getSeconds()}`
  let result = ``
  let patternValue = patterns[pattern]
  if (patternValue) {
    if (typeof patternValue !== 'string') {
      if (pattern.split(' ')[1]) {
        result = `${year}${patternValue[0]}${month}${patternValue[1]}${day}${patternValue[2]} ${hour}:${minute}:${seconds}`
      } else {
        result = `${year}${patternValue[0]}${month}${patternValue[1]}${day}${patternValue[2]}`
      }
    } else {
      if (pattern.split(' ')[1]) {
        result = `${year}${patternValue}${month}${patternValue}${day} ${hour}:${minute}:${seconds}`
      } else {
        result = `${year}${patternValue}${month}${patternValue}${day}`
      }
    }
  } else {
    console.error(`该方法不接受"${pattern}"日期格式`)
  }
  return result
}
