export default {
  path: '/myAccount',
  name: '我的账户',
  component: resolve => require(['../views/myAccount/MyAccount.vue'], resolve),
  redirect: '/accountInfo',
  children:
  [
    {
      path: '/accountInfo',
      name: '账户信息',
      component: resolve => require(['../views/myAccount/accountInfo/AccountInfo.vue'], resolve)
    },
    {
      path: '/withdraw',
      name: '提现',
      component: resolve => require(['../views/myAccount/accountInfo/Withdraw.vue'], resolve)
    }
  ]
}



